import uniRate from "../../components/uni-rate/uni-rate.vue"
export default {
	data() {
		return {
			list:[
				{title:"中餐",list:["盖饭","扒饭","咖喱饭","鸡排饭"]},
				{title:"西餐",list:["牛排","意面","芝士","汉堡"]},
				{title:"法餐",list:["辣子鸡丁","xxx","肥仔快乐水","填缝隙"]},
				{title:"快餐",list:["薯条","丸子","粥","饮料"]},
			],
			clickId:"",
			currentNum:0,
			topList:[],
			text:"xxx",
			name:this.$store.state.name
		}
	},
	components:{
		uniRate
	},
	computed:{
		num(){
			return this.$store.state.num;
		},
		count(){
			return this.$store.getters.count;
		},
		testList(){
			return this.$store.state.testList;
		}
	},
	onReady(){
		this.getNodesInfo();
		
		// 测试请求
		this.testRequest();
	},
	methods: {
		setId(index){
			this.clickId="po"+index;
			this.currentNum=index;
		},
		scroll(e){
			let scrollTop=e.target.scrollTop;
			for(let i=0;i<this.topList.length;i++){
				let h1=this.topList[i];
				let h2=this.topList[i+1];
				if(scrollTop>=h1&&scrollTop<h2){
					this.currentNum=i;
				}
			}
		},
		scrolltolower(){
				setTimeout(()=>{
					this.currentNum=3;
				}, 80);
		},
		add(){
			this.$store.commit("add");
		},
		testActions(){
			this.$store.dispatch("testActions");
		},
		getNodesInfo(){
			// console.log(document)
			// console.log(window)
			const query = uni.createSelectorQuery().in(this);
			// document.querySelector
			// document.querySelectorAll
			// query.selectAll('.title').boundingClientRect().selectAll('.box2').boundingClientRect().exec((res)=>{
			// 	console.log(res);
			// });
			query.selectAll('.title').boundingClientRect().exec((res)=>{
				let nodes=res[0];
				let rel=[];
				nodes.map(item=>{
					rel.push(item.top)
				})
				this.topList=rel;
			});
		},
		testRequest(){
			uni.request({
			    url: 'https://bird.ioliu.cn/weather', //仅为示例，并非真实接口地址。
			    data: {
			        city: '北京'
			    },
			    header: {
			        // 'token': 'xxx' //自定义请求头信息
			    },
			    success: (res) => {
			        // this.text = 'request success';
					console.log(res);
			    }
			});
		}
		
	}
}
